import React from "react";

export default function DeliveryDetails(props) {
  const pickwarehouse = props.pickwar;
  const parceldetail = props.pardet;
  return (
    <div className="row">
      <div className="col-lg-6">
        <div className="special-box">
          <div className="heading-area">
            <h4 className="title">Pickup Details</h4>
          </div>
          <div className="table-responsive-sm">
            <table className="table">
              <tbody>
                <tr>
                  <th width="45%">Warehouse ID</th>
                  <th width="10%">:</th>
                  <td width="65%">{pickwarehouse.id}</td>
                </tr>
                <tr>
                  <th width="45%">Warehouse Name</th>
                  <th width="10%">:</th>
                  <td width="65%">{pickwarehouse.name}</td>
                </tr>
                <tr>
                  <th width="35%">Address</th>
                  <th width="10%">:</th>
                  <td width="65%"></td>
                </tr>
                <tr>
                  <th width="35%">Area</th>
                  <th width="10%">:</th>
                  <td width="65%">{pickwarehouse.area}</td>
                </tr>
                <tr>
                  <th width="35%">City</th>
                  <th width="10%">:</th>
                  <td width="65%">{pickwarehouse.district}</td>
                </tr>
                <tr>
                  <th width="45%">Postal Code</th>
                  <th width="10%">:</th>
                  <td width="65%">{pickwarehouse.post_code}</td>
                </tr>
                <tr>
                  <th width="35%">Country</th>
                  <th width="10%">:</th>
                  <td width="65%">{pickwarehouse.country}</td>
                </tr>
                {/* <tr>
                  <th width="45%">Pickup Status</th>
                  <th width="10%">:</th>
                  <td width="65%">{parceldetails.status}</td>
                </tr> */}
              </tbody>
              <br />
            </table>
          </div>
        </div>
      </div>

      <div className="col-lg-6">
        <div className="special-box">
          <div className="heading-area">
            <h4 className="title">Delivery Details</h4>
          </div>
          <div className="table-responsive-sm">
            <table className="table">
              <tbody>
                <tr>
                  <th width="40%">
                    <strong>Name</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%">{parceldetail.customer_name}</td>
                </tr>
                <tr>
                  <th width="35%">
                    <strong>Phone</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%">{parceldetail.customer_number}</td>
                </tr>
                <tr>
                  <th width="35%">
                    <strong>Address</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%"></td>
                </tr>
                <tr>
                  <th width="35%">
                    <strong>Area</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%">{parceldetail.customer_address}</td>
                </tr>
                <tr>
                  <th width="35%">
                    <strong>City</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%">{parceldetail.customer_city}</td>
                </tr>
                <tr>
                  <th width="35%">
                    <strong>Postal Code</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%">{parceldetail.zip_code}</td>
                </tr>
                <tr>
                  <th width="35%">
                    <strong>Country</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%"></td>
                </tr>
                {/* <tr>
                  <th width="35%">
                    <strong>Delivery Status</strong>
                  </th>
                  <th width="10%">:</th>
                  <td width="65%">{parceldetails.status}</td>
                </tr> */}
              </tbody>
              <br />
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}
