import axios from "axios";
import React, { useState, useEffect } from "react";
import * as Helper from "../Utility/Helper";
import Loader from "../Utility/Loader";
import { DELIVERYMAN, EDITPARCEL } from "../../axios/Api";
import Swal from "sweetalert2";
import DeliveryDetails from "./DeliveryDetails";
import ParcelDetails from "./ParcelDetails";

export default function Deliveryman() {
  const [deliverydetails, setDeliverydetails] = useState([]);
  const [parceldetails, setParceldetails] = useState([]);
  const [status, setStatus] = useState("");
  const [id, setId] = useState("");
  const [show, setShow] = useState(false);
  let orderdetails = [];
  let pickupwarehouse = [];
  let deliverylocation = [];
  let product = [];
  useEffect(() => {
    // console.log(status);

    axios.patch("https://10.100.17.47/FairEx/api/v1/admin/parcel/" + id, {
      status: status,
    });
  }, [status, id]);
  useEffect(() => {
    axios
      .get(DELIVERYMAN)
      .then((response) => {
        setDeliverydetails(response.data);
      })
      .catch(function (error) {
        Helper.alertMessage("error", "Something went wrong!");
      });
  }, [status]);
  if (deliverydetails != "") {
    // console.log(deliverydetails);
    orderdetails = deliverydetails.data;
  }

  function showDetails(details) {
    setShow(!show);
    setParceldetails(details);
  }
  if (parceldetails != "") {
    // console.log(deliverydetails);
    pickupwarehouse = parceldetails.pickup_warehouse;
    deliverylocation = parceldetails.delivery_location;
    product = parceldetails.product;
  }

  return (
    <div>
      <div className="container-fluid p-0">
        {!show && <h1 className="h3">Order List for Delivery</h1>}
        {!show && (
          <div className="card">
            <div className="card-body">
              <div className="table-responsive">
                <table id="myTable" className="display table table-striped">
                  <thead>
                    <tr>
                      <th>Serial #</th>
                      <th>Order No.</th>
                      <th>Priority Level</th>
                      <th>Pickup Warehouse</th>
                      <th>Delivery Location</th>
                      <th>Order Status</th>
                      <th>Change Status</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {orderdetails.map((val, index) => (
                      <tr>
                        <td> {index + 1}</td>
                        <td> {val.parcel.order_no}</td>
                        <td> {console.log(val.parcel)}</td>
                        <td> {val.parcel.pickup_warehouse.name}</td>
                        <td> {val.parcel.delivery_location.area}</td>
                        <td> {val.parcel.status}</td>
                        <td>
                          <select
                            className="btn btn-sm btn-info"
                            onChange={(event) => {
                              setStatus(event.target.value);
                              setId(val.parcel.id);
                            }}
                          >
                            <option>Choose from Here</option>
                            return (<option value="Picked">Picked</option>
                            <option value="Delivered">Delivered</option>
                            <option value="Cancelled">Cancelled</option>
                            ); )
                          </select>
                        </td>
                        <td>
                          <button
                            onClick={() => {
                              showDetails(val.parcel);
                            }}
                            className="btn btn-success btn-sm me-2"
                          >
                            Details
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        )}

        {show && (
          <div>
            <button
              onClick={() => {
                setShow(!show);
              }}
              className="btn btn-success btn-sm me-2"
            >
              Return to Order List
            </button>
          </div>
        )}
        <br />
        <br />
        {show && <ParcelDetails product={product} />}
        <br />
        <br />
        {show && (
          <DeliveryDetails pickwar={pickupwarehouse} pardet={parceldetails} />
        )}
      </div>
    </div>
  );
}
